/* @import 'react-loader-spinner/dist/loader/css/react-spinner-loader.css'; */

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
section {
  text-align: center;
}

input:focus {
  color: dimgrey;
  border: none;
  font-style: italic;
  outline: none;
  line-height: 1.5;
}
ul {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  align-items: flex-start;
}
li {
  list-style: none;
  position: relative;
  margin-bottom: 10px;
  width: 350px;
  height: 263px;
}

img {
  height: 100%;
  width: 100%;
  cursor: pointer;
  object-fit: cover;
}
.LoadMoreBtn {
  margin-bottom: 15px;
  background-color: #4354b0;
  border-radius: 3px;
  border: none;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-size: 13px;
  padding: 10px 24px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #154682;
}
.LoadMoreBtn:active {
  position: relative;
  top: 1px;
  border: none;
  outline: none;
}
.LoadMoreBtn:hover {
  border: 1px solid #4354b0;
}
.Xbtn {
  background: #ffff;
  color: grey;
  border: 1px solid grey;
  border-radius: 3px;
  float: right;
}
.Backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.Modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-height: 300px;
  max-width: 600px;
  width: 100%;
  padding: 12px;
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}
